import React, { useState, useEffect } from 'react';
import { PopupButton } from 'react-calendly';
import { Link } from 'react-router-dom';

export default () => {
  const [eventTypes, setEventTypes] = useState([]);

  useEffect(() => {
    fetch('/api/event_types')
      .then((res) => res.json())
      .then((result) => {
          console.log("your eventtypes " + JSON.stringify(result.eventTypes))
        setEventTypes(result.eventTypes);
      });
  }, []);

  return (
    <div className="container" style={{ marginTop: '50px' }}>
      <div className="row">
          {console.log("mapping event types " + JSON.stringify(eventTypes))}
        {eventTypes.map(
          (eventType) =>
            eventType.active === true && (
              <div className="col s6" key={eventType.uri}>
                <Link to={`/event_types/${eventType.uri.split('/')[4]}`}>
                  <div className="card">
                    <div
                      style={{
                        backgroundColor: eventType.color,
                        height: 50,
                        width: '100%',
                      }}
                    >Add an Event</div>
                    <div className="card-content" style={{ color: 'black' }}>
                      <p>{eventType.name}</p>
                      <p style={{ fontSize: 'small' }}>
                        Description:{' '}
                        {eventType.description_plain || 'No description'}
                      </p>
                    </div>
                    <div className="card-action">
                      <PopupButton
                        url={eventType.scheduling_url}
                        rootElement={document.getElementById('root')}
                        text="View Availbility"
                        styles={{
                          borderWidth: 0,
                          backgroundColor: '#fff',
                          cursor: 'pointer',
                        }}
                      />
                    </div>
                  </div>
                </Link>
              </div>
            )
        )}
      </div>
    </div>
  );
};
