import React, { Component, useState } from "react";
import {BrowserRouter, Link, Route, Routes} from "react-router-dom";
import Dashboard from './dashboard.jsx';
import Events from './events.jsx';
import EventType from './eventType';
import Invitee from './invitee.jsx';
import Login from './login.jsx';
import Nav from './nav.jsx';
import { RequireAuth, AuthProvider } from './auth.jsx';
import Event from './event.jsx';
import EventTypeAvailTimes from './eventTypeAvailTimes.jsx';
import UserBusyTimes from "./userBusyTimes";
import Error from './Error'


const Router = (props) => {
  const [duration, setduration] = useState("1000");
  const [toggledOn, settoggledon] = useState(false);


console.log("ROUTER PROPS " + JSON.stringify(props))
    return (
    <BrowserRouter>
        <AuthProvider>
                  <Nav />
  <Routes>
    <Route path="/login" element={<Login />} />
    <Route
      path="/"
      element={
        <RequireAuth>
          <Dashboard />
        </RequireAuth>
      }
    />
    <Route
      exact
      path="/events"
      element={
        <RequireAuth>
          <Events />
        </RequireAuth>
      }
    />
    <Route
      path="/event_types/:uuid"
      element={
        <RequireAuth>
          <EventType />
        </RequireAuth>
      }
    />
    <Route
      path="/events/:uuid"
      element={
        <RequireAuth>
          <Event />
        </RequireAuth>
      }
    />
    <Route
      path="/events/:uuid/invitees"
      element={
        <RequireAuth>
          <Invitee />
        </RequireAuth>
      }
    />
    <Route path="/event_type_available_times"
    element={
      <RequireAuth>
        <EventTypeAvailTimes />
      </RequireAuth>
    }
    />
    <Route path="/user_busy_times"
    element={
      <RequireAuth>
        <UserBusyTimes />
      </RequireAuth>
    }
    />
    <Route path='*' element={<Error/>} />
  </Routes>
            </AuthProvider>
    </BrowserRouter>
    );
  }



export default Router;


